import React from "react";
import Content from "./Content";
class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      num: 0
    };
  }
  setNumber = () => {
    this.setState({ num: this.state.num + 1 });
  };
  render() {
    return (
      <div>
        <button onClick={this.setNumber}>increment</button>
        <Content myNumber={this.state.num} />
      </div>
    );
  }
}
export default App;
